.cartPage {
    padding: 5vmax;
    padding-top: 6vmax;
}
.cartHeader {
    background-color: tomato;
    width: 90%;
    margin: auto;
    display: grid;
    box-sizing: border-box;
    padding: 10px 10px;
    grid-template-columns: 4fr 1fr 1fr;
    color: aliceblue;
    font-size: x-small;
}
.cart_container {
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
    border-bottom: 1px solid silver ;
}
.quantity_input {
    display: flex;
    align-items: center;
    height: 8vmax;
}
.quantity_input > button {
    width: 30px;
    height: 40px;
    font-size: 30px;
    background-color: #928b8b;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.quantity_input > input {
    width: 60px;
    height: 40px;
    background-color: transparent;
    border: none;
    color: aliceblue;
    padding-left: 10px;
}
.subtotal {
    display: flex;
    align-items: center;
    height: 8vmax;
    color: aliceblue;
    font-size: medium;
}
.grosstotal {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    gap: 20px;
    color: aliceblue;
    margin: auto;
    margin-top: 20px;
}
.grosstotal .total_line {
    width: 50%;
    height: 4px;
    background-color: tomato;
}
.grosstotal button {
    width: 200px;
    height: 40px;
    border-radius: 20px;
    background-color: tomato;
    font-size: larger;
    color: aliceblue;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
    border: none;
}
.grosstotal button:hover {
    background-color: rgb(179, 66, 46);

}
.grosstotal p {
    font-size: larger;
    text-align: start;
}
.no_item_in_cart {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 8vmax;
    color: aliceblue;
    gap: 40px;
}
.no_item_in_cart h1 {
    font-size: 60px;
}
.no_item_in_cart button {
    width: 300px;
    background-color: tomato;
    color: black;
    font-size: 30px;
    font-weight: 600;
    outline: none;
    border: none;
    padding: 15px;
    cursor: pointer;
    transition: all 0.5s;
}
.no_item_in_cart button:hover {
    background-color: rgb(179, 66, 46);
}

/* //////// */

.CartItemCard {
    display: flex;
    padding: 1vmax;
    height: 8vmax;
    align-items: flex-start;
    box-sizing: border-box;
  }
  .CartItemCard > img {
    height: 7vmax;
  }
  
  .CartItemCard > div {
    display: flex;
    margin: 0.3vmax 1vmax;
    flex-direction: column;
    gap: 4px;
  }
  
  .CartItemCard > div > a {
    font: 300 1.2vmax cursive;
    color: rgba(251, 251, 251, 0.815);
    text-decoration: none;
  }
  
  .CartItemCard > div > span {
    font: 300 0.9vmax "Roboto";
    color: rgba(251, 251, 251, 0.815);
  }
  
  .CartItemCard > div > p {
    color: tomato;
    font: 100 0.8vmax "Roboto";
    cursor: pointer;
  }

  @media screen and (max-width:600px) {
    .cartPage {
        padding-left: 0;
        padding-right: 0;
        padding-top: 10vmax;
    }
  }