
/* //////////////////////////////////////////////////////// */

a {
    text-decoration: none;
}

.home {
    width: 100vw;
    min-height: 100vh;
    background-color: black;
}
.main {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    /* background: linear-gradient(rgb(5, 241, 68),rgb(0, 0, 0)); */
    /* clip-path: polygon(50% 0%, 100% 0, 100% 100%, 90% 88%, 10% 88%, 0 100%, 0 0); */
    overflow: hidden;


}
.main > h1{
    /* position: absolute; */
    font-size: 15vw;
    /* color: rgb(202,179,168); */
    filter: drop-shadow(0 100px 50px black);
    
}
.animate {
    animation: bounce-in-top 1.1s both;
    animation-delay: 0.8s;
}
.main > img {
    position: absolute;
    width: 23vw;
    filter: drop-shadow(0,0,10,rgb(47, 97, 141));
    animation: slide-in-elliptic-top-fwd 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
@keyframes slide-in-elliptic-top-fwd {
    0% {
        transform: translateY(-600px) rotateX(-30deg) scale(0);
        transform-origin: 50% 100%;
        opacity: 0;
    }
    100% {
        transform: translateY(0) rotateX(0) scale(1);
        transform-origin: 50% 1400px;
        opacity: 1;
    }
  }
  
  @keyframes bounce-in-top {
    0% {
              transform: translateY(-500px);
              animation-timing-function: ease-in;
              opacity: 0;
    }
    38% {
              transform: translateY(0);
              animation-timing-function: ease-out;
              opacity: 1;
    }
    55% {
              transform: translateY(-65px);
              animation-timing-function: ease-in;
    }
    72% {
              transform: translateY(0);
              animation-timing-function: ease-out;
    }
    81% {
              transform: translateY(-28px);
              animation-timing-function: ease-in;
    }
    90% {
              transform: translateY(0);
              animation-timing-function: ease-out;
    }
    95% {
              transform: translateY(-8px);
              animation-timing-function: ease-in;
    }
    100% {
              transform: translateY(0);
              animation-timing-function: ease-out;
    }
  }

