  .explore {
    position: relative;
    top: -6vw;
    width: 100vw;
    min-height: 100vh;
    background-color: #011010;
    background: linear-gradient(135deg,#0b2e2e,rgb(0, 0, 0));
    clip-path: polygon(10% 0, 90% 0, 100% 2%, 100% 100%, 0 100%, 0 2%);
    border: 10px solid rgba(255, 255, 255, 0.56);
    border-top: none;
    overflow-x: hidden;
  }
  .explore > .explore_head {
    display: flex;
    justify-content: center;
    align-items: center;
    color: color-mix(in srgb, #34c9eb 30%, white);
  }
  .explore > .explore_head > h1 {
    font-size: 4.5vw;
  }
  .featured_head h1{
    color: rgb(230, 232, 233);
    font-size: 8vw;
    font-family: 'Monoton', cursive;
    margin-top: 4vw;
    margin-left: 10vw;
    transition: transform 0.3s ease-out;
  }
  .react-parallax-outer-inner-h1 {
    transform: translateX(var(--parallax-x)px);
  }
  .featured_head h2{
    color: rgb(230, 232, 233);
    font-size: 8vw;
    font-family: 'Monoton', cursive;
    margin-left: 38vw;
  }

  .featured_product {
    display: flex;
    max-width: 80vw;
    justify-content: flex-start;
    margin: 0 auto;
    padding: 8vw 4vw;
    gap: 6vw;
    overflow-y: hidden;
    /* overflow: scroll; */
    /* padding-bottom: 1000px; */
  }

  .banner1 {
    width: 98%;
    height: 450px;
    background: url("../../../assests/banner2.jpeg");
    background-size: cover;
    margin: 70px auto;
    filter: brightness(70%);
    filter: hue-rotate(171deg);
    border-radius: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    padding: 6vmax;
    gap: 60px;
    color: aliceblue;
    border: 2px solid whitesmoke;
  }
  .banner1 h1 {
    font-size: 50px;
  }
  .banner1 h2 {
    font-size: 50px;
  }
  .banner1 button {
    width: 300px;
    padding: 20px;
    font-size: 20px;
    cursor: pointer;
  }

  .category_head {
    display: flex;
    justify-content: center;
  }
  
  .category_head h1{
    color: rgb(230, 232, 233);
    font-size: 7vw;
    font-family: 'Monoton', cursive;
    margin-top: 4vw;
  }

  .category_cont {
    display: flex;
    max-width: 90vw;
    justify-content: center;
    margin: 10vh auto;
    gap: 3vw;
    flex-wrap: wrap;
  }

  .category {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #b6aeae24;
    padding: 2rem 5rem;
    box-shadow: 0 0 30px black;
  }

  .category_cont .category img {
      width: 13rem;
      transition: ease .5s;
      cursor: pointer;
  }

  .category_cont .category img:hover {
    transform: scale3d(1.4,1.4,1.4);
    filter: drop-shadow(0 0 40px black);
  }
  .product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 300px;
    height: 400px;
    padding: 10px;
    /* border: 1px solid rgb(84, 104, 105); */
    position: relative;
    /* background-color: #181717; */
    background-color: white;
    border-radius: 10px;
    /* box-shadow: 0 0 20px rgb(0, 0, 0); */
    /* border: 5px solid black; */
    transition:cubic-bezier(0.1, 1, 0.320, 1) 1s;
  }

  .product img {
    /* border-radius: 4vh; */
    max-width: 300px;
    max-height: 300px;
    border-radius: 10px;
  }
  .content p {
    color: rgb(0, 0, 0);
    font-size: 30px;
    font-weight:700 ;
    border-top: 1px solid black;
    margin-top: 20px;
  }
  .content .star {
    display: flex;
    color: aliceblue;
    align-items: center;
    gap: 3px;
  }
  .content span {
    color: rgb(0, 0, 0);
    font-size: large;
  }
  .product:hover {
    transform:translateY(-20px)
  }

