  }

  .see_all {
    width: 300px;
    background-color: tomato;
    border-radius: 40px;
    margin: 30px auto;
    transition: all 0.5s;
  }
  .see_all button {
    width: 100%;
    height: 100%;
    border-radius: 40px;
    font-size: 20px;
    padding: 20px;
    cursor: pointer;
    background-color: transparent;
