  }

  .header_container {
    width: 90vw;
    height: 20vh;
    margin: auto;
    background-color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: aliceblue;
    padding: 20px 0;
    border-top: 1px solid white;
  }
  .social_icons {
    display: flex;
    gap: 20px;
  }
  .social_icons .main_icons {
    font-size: 40px;
  }
  .main_social {
    position: absolute;
    left: 30px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    color: white;
  }
  .main_social .main_icons {
    font-size: 40px;
    cursor: pointer;
    transition: all 0.5s;
  }
  .explore .arrow_down {
