.loader_main {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
}
.loader {
    width: 200px;
    height: 200px;
    border-radius: 100%;
    border-top: 10px solid white;
    animation: rotate 1s infinite;
}
@keyframes rotate {
    100% {
        rotate: 360deg;
    }
}

@media screen and (max-width:600px) {
    .loader {
        width: 50px;
        height: 50px;
        border-top: 2px solid white;
    }
}