.bod {
    width: 100vw;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(rgb(1, 31, 18),rgb(1, 54, 25),rgb(2, 61, 50));
}
.LS_main {
    width: 350px;
    height: 500px;
    background: red;
    overflow: hidden;
    background: url('../../assests/1.jpg') no-repeat center;
    border-radius: 10px;
    box-shadow: 0 0 80px #000;
    /* border: 1px solid rgb(6, 246, 18); */
}
#chk {
    display: none;
}
.signup {
    position: relative;
    width: 100%;
    height: 100%;
}
.LS_main  label {
    color: aliceblue;
    font-size: 2.3rem;
    display: flex;
    justify-content: center;
    margin: 60px;
    font-weight: bold;
    cursor: pointer;
    transition: .5s ease-in-out;
}
.LS_main input {
    width: 60%;
    height: 30px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    margin: 20px auto;
    padding: 10px;
    border: none;
    outline: none;
    border-radius: 5px;
}
.LS_main button {
    width: 60%;
    height: 40px;
    margin: 10px auto;
    display: block;
    justify-content: center;
    color: #fff;
    background: #08be08;
    font-size: 1em;
    font-weight: bold;
    margin-top: 20px;
    outline: none;
    border: none;
    border-radius: 5px;
    transition: .2s ease-in;
    cursor: pointer;
}
.LS_main button:hover {
    background: #099309;
}

.signin {
    position: relative;
    top: -75px;
    height: 460px;
    background: #ffffff;
    border-radius: 60% / 10%;
    transform: translateY(-100px);
    transition: .8s ease-in-out;
}
.signin label {
    color: #08be08;
    transform: scale(.6);
}
#chk:checked ~ .signin {
    transform: translateY(-430px);
}
#chk:checked ~ .signin label {
    transform: scale(1);
}
#chk:checked ~ .signup label {
    transform: scale(.6);
}
.MuiAlert-root {
    position: absolute;
    width: 350px;
}

.registerImage {
    display: flex;
    align-items: center;
    margin: 0 70px;
}
.registerImage > img {
    width: 50px;
    height: 50px;
    border-radius: 100%;
}
.registerImage > input {
    margin: 0;
    width: 140px;
    margin-left: 20px;
}
#file-upload-button {
    cursor: pointer;
}
.signin p {
    margin-left: 20%;
}