.payment_main {
    padding: 6vmax;
    padding-bottom: 0;
}
.payment_container {
    display: grid;
    place-items: center;
    height: 65vh;
    margin: 2vmax;
}
.paymentForm {
    width: 32%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #eeeaeaac;
    padding: 20px;
    border-radius: 20px;
}
.paymentForm h1 {
    color: rgb(0, 0, 0);
}
.paymentForm > div {
    color: aliceblue;
    width: 100%;
    display: flex;
    align-items: center;
    margin: 2vmax 0;
}
.paymentForm  p {
    font: 400 2vmax "Roboto";
    color: rgba(0, 0, 0, 0.753);
    border-bottom: 1px solid rgba(0, 0, 0, 0.13);
    padding: 1vmax 0;
    text-align: center;
    width: 50%;
    margin: auto;
}
.paymentInput {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.474);
    border-radius: 4px;
    outline: none;
    color: aliceblue;
}
.paymentForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}
.paymentFormBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
}
.paymentFormBtn:hover {
    background-color: rgb(179, 66, 46);
}
CardNumberElement {
    color: aliceblue;
}

@media screen and (max-width: 600px) {
    .payment_main {
        padding-top: 8vmax;
    }
    .paymentForm {
        width: 100%;
    }
}