.update_main {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.edit {
    border: 1px solid white;
    width: 400px;
    height: 560px;
    padding: 40px 50px;

}
.edit form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 40px;

}
.edit form input {
    height: 40px;
    padding-left: 10px;
    border-radius: 20px;
    border: none;
}
.updateImage input {
    border-radius: 0;
}