.success_main {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
}
.task_icon {
    font-size: 200px;
    color: tomato;
}
.success_main h1 {
    font-size: 50px;
    color: aliceblue;
}
.success_link button {
    background-color: tomato;
    padding: 20px;
    width: 300px;
    font-size: 20px;
    font-weight: 800;
    cursor: pointer;
    transition: all 0.5s;
    border: none;
}
.success_link button:hover {
    background-color: rgb(210, 74, 50);
}


@media screen and (max-width: 600px) {
    
    .success_main h1 {
        font-size: 30px;
        color: aliceblue;
    }
}