.profile_main {
    display: flex;
    margin: 0 20px;
    margin-top: 100px;
    color: aliceblue;
    justify-content: center;
    align-items: center;
    background-color: #474040;
    box-shadow: 0 0 30px rgb(68, 66, 66);
}
.profile_left {
    width: 50%;
    /* border: 1px solid rgb(240, 232, 4); */
    display: flex;
    flex-direction: column;
    gap: 30px;
    justify-content: center;
    align-items: center;
}
.profile_img {
    padding: 10px;
    background: linear-gradient(rgb(246, 121, 4),rgb(0, 217, 255));
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.profile_left  img {
    width: 350px;
    height: 350px;
    border-radius: 100%;
    margin: 0;
    box-shadow: 0 0 0 1px black;
    box-shadow: 0 0 0 3px transparent;
    box-shadow: 0 0 0 5px black;
    transition: transform 1s;
}
.profile_left  img:hover {
    transform: scale(1.1);
}
.profile_left > h1 {
    font-size: 50px;
    font-family: 'Tektur', cursive;
    color: rgb(0, 217, 255);
}
.profile_left > h1 span{
    color: rgb(246, 121, 4);
}
.edit_profile {
    text-align: center;
    color: rgb(0, 0, 0);
    font-size: 30px;
    font-weight: 900;
    background-color: rgb(0, 217, 255);
    width: 300px;
    height: 35px;
    border-radius: 15px;
}
.profile_right {
    width: 50%;
    /* border: 1px solid rgb(240, 232, 4); */
    color: rgb(240, 232, 4);
    display: flex;
    flex-direction: column;
    align-items: center;
    /* font-family: 'Archivo', sans-serif; */
    font-family: 'Tektur', cursive;
}
.profile_right h4 {
    font-size: 30px;
    margin: 20px 0;
    text-align: center;
    margin-top: 0;
}
.profile_right p {
    font-size: 25px;
    margin: 15px 0;
    text-align: center;
}
.profile_right div {
    text-align: center;
    margin: 15px 0;
    border-bottom: 2px solid white;
}
.profile_right .buttons {
    display: flex;
    flex-direction: column;
    border-bottom: none;
    width: 60%;
}
.orders {
    text-align: center;
    color: rgb(0, 0, 0);
    font-size: 20px;
    font-weight: 900;
    background-color: rgb(246, 121, 4);
    /* width: 400px; */
    height: 35px;
    border-radius: 15px;
}
.change_password {
    text-align: center;
    color: rgb(0, 0, 0);
    font-size: 20px;
    font-weight: 900;
    background-color: rgb(246, 121, 4);
    /* width: 400px; */
    height: 35px;
    border-radius: 15px;
    margin-top: 40px;
}
.line {
    width: 1px;
    height: 400px;
    background-color: #fff;
}

@media screen and (max-width:600px) {
    .profile_main {
        flex-direction: column;
    }
    .profile_left img {
        width: 150px;
        height: 150px;
    }
    .line {
        height: 1px;
        width: 80vw;
        margin-top: 40px;
    }
    .profile_left > h1 {
        font-size: 30px;
    }
    .profile_right {
        width: 100%;
    }
    .profile_right .buttons {
        width: 80%;
    }
}
