.all_product {
    width: 80%;
    margin-top: 100px;
    margin-left: 10px;
    background-color: #39393c;
    padding: 30px 0;
    border-radius: 50px;
}
.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
    color: aliceblue;
}
.pagination button {
    color: aliceblue;
}
.pagination li {
    color: aliceblue;
}
.mainn {
    display: flex;
}
.filter {
    height: 80vh;
    width: 250px;
    margin-top: 100px;
    border: 2px solid white;
    border-radius: 20px;
    margin-left: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    color: aliceblue;
    padding: 20px 0;
}
.filter_1 {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.mainn ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.mainn ul h1 {
    margin: 10px 0;
}
.mainn li {
    text-decoration: none;
    cursor: pointer;
}
@media screen and (max-width:600px) {
    .mainn {
        flex-direction: column;
    }
    .all_product {
        margin: auto;
        margin-top: 20px;
        border-radius: 0;
    }
    .filter {
        width: 90vw;
        margin: auto;
        height: fit-content;
        margin-top: 50px;
    }
    
.mainn ul {
    gap: 10px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.mainn ul h1 {
    margin: 0;
}
.mainn li {
    text-decoration: none;
    cursor: pointer;
}
.filter > h1 {
    margin-top: 0;
}
}