.confirm_main {
    padding: 6vmax;
    padding-bottom: 0;
    color: aliceblue;
}
.confirm_cont {
    display: flex;
    margin-top: 20px;
}
.confirm_left {
    width: 70%;
    border-right: 1px solid whitesmoke;
    
}
.confirm_left > div {
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.confirm_left > div .items {
    display: grid;
    grid-template-columns: 1fr 1fr 4fr;
    align-items: center;
    justify-content: end;
}
.confirm_left > div .items img {
    width: 60px;
}
.confirm_left > div > h2 {
    font-size: 40px;
    margin-bottom: 10px;
}
.confirm_right {
    margin-left: 30px;
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.confirm_right span {
    font-size: larger;
}
.confirm_right h1 {
    font-size: 40px;
    padding-bottom: 5px;
    border-bottom: 1px solid rgba(245, 222, 179, 0.517);
}
.confirm_right > div {
    display: flex;
    justify-content: space-between;
}
.confirm_right .total {
    padding-top: 10px;
    border-top: 1px solid rgba(245, 222, 179, 0.517);
}
.confirm_right button {
    width: 100%;
    height: 50px;
    border-radius: 40px;
    background-color: tomato;
    color: black;
    border: none;
    cursor: pointer;
    font-size: 15px;
    font-weight: 800;
    transition: all 0.5s;
    box-shadow: 0 0 0 1px rgb(108, 90, 90);
    box-shadow: 0 0 0 2px rgb(255, 255, 255);
}
.confirm_right button:hover {
    background-color: rgb(179, 66, 46);
}

@media screen and (max-width: 600px) {
    .confirm_cont {
        flex-direction: column;
        padding-bottom: 3vmax;
    }
    .confirm_left {
        width: 100%;
        border: none;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
.confirm_left > div {
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.confirm_left > div .items {
    display: grid;
    grid-template-columns: 1fr 1fr 4fr;
    align-items: center;
    justify-content: end;
}
.confirm_left > div .items h2 {
    font-size: 10px;
    margin-right: 20px;
    margin-left: 20px;
}
.confirm_left > div .items img {
    width: 40px;
}
.confirm_left > div > h2 {
    font-size: 30px;
    margin-bottom: 10px;
    border-bottom: 1px solid white;
}
}