.shipping_main {
    padding: 6vmax;
    padding-bottom: 0;
    color: aliceblue;
}
.shippingForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin: auto;
  padding: 2vmax;
  padding-bottom: 0;
  justify-content: space-evenly;
  height: 80%;
  transition: all 0.5s;
}
.shippingForm > div {
    display: flex;
    width: 100%;
    align-items: center;
}
.shippingForm > div > input,
.shippingForm > div > select {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax cursive;
  outline: none;
  background-color: #b9b7b7;
}
.shippingForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}
.shippingBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1vmax "Roboto";
    width: 100%;
    padding: 1vmax;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
    margin: 2vmax;
}
.shippingBtn:hover {
    background-color: rgb(179, 66, 46);
}

@media screen and (max-width: 600px) {
  
    .shippingForm {
      padding: 11vw;
    }
  
    .shippingForm > div > input,
    .shippingForm > div > select {
      padding: 5vw 10vw;
      font: 300 4vw cursive;
    }
  
    .shippingForm > div > svg {
      font-size: 6vw;
      transform: translateX(3vw);
    }
  
    .shippingBtn {
      font: 300 4vw "Roboto";
      padding: 4vw;
    }
    .shipping_main {
      padding: 1vmax;
      padding-top: 8%;
    }
  }