body {
    background-color: #000000;
}

.productDetail {
    width: 100vw;
    min-height: 100vh;
    background-color: #272a2b;
    padding: 6vmax;
    box-sizing: border-box;
    display: flex;
    align-items: center;
}
.left {
    width: 50%;
    height: 80%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 40px;
    background-color: #ffffff;
    border-radius: 20px;
    /* display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; */
    /* border: 1px solid rgb(218, 215, 215); */
}
.asdfg {
    display: flex;
    justify-content: center;
    align-items: center;
}
.left img {
    max-height: 70%;
    max-width: 24rem;
}
.right {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 40%;
    height: 80%;
    /* border: 1px solid rgb(198, 193, 193); */
    display: flex;
    flex-direction: column;
    color: aliceblue;
    word-wrap: break-word;
    max-height: 500px;
    text-overflow: ellipsis;
}
.rating_block {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
price {
    font-size: 40px;
    font-weight: bolder;
    color: rgb(255, 51, 0);
}
.buy {
    display: flex;
    align-items: center;
    gap: 5px;
}
.buy button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: rgb(255, 51, 0);
    color: aliceblue;
    font-size: 30px;
    cursor: pointer;
}
.buy button:hover {
    background-color: rgb(155, 34, 4);
}
.buy input {
    width: 50px;
    height: 30px;
    border-radius: 10px;
    text-align: center;
    background-color: #ba1010;
    color: aliceblue;
    font-weight: bolder;
    border: 1px solid whitesmoke;
}
.buy .atc {
    width: fit-content;
    height: fit-content;
    border-radius: 10px;
    padding: 5px 8px;
    font-size: 25px;
    border: 2px solid white;
}
.description {
    font-size: larger;
}
.reviews {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    top: -6vw;
    width: 100vw;
    min-height: 100vh;
    background-color: #000000;
    clip-path: polygon(25% 0%, 75% 0%, 100% 11%, 100% 100%, 0 100%, 0 11%);

}
.reviews .head {
    display: flex;
    justify-content: center;
    color: aqua;
    font-size: 2.5vw;
}
.noReviews {
    color: aliceblue;
    font-size: 40px;
    margin-top: 100px;
}
.card {
    display: flex;
    flex-direction: column;
    gap: 15px;
    max-width: 500px;
    background-color: #1d1b1b;
    color: aliceblue;
    border: 2px solid rgb(87, 83, 83);
    padding: 20px;
    word-wrap: break-word;
    border-radius: 20px;
    margin: 25px 0;
}
.review_cont {
    margin-top: 30px;
}
.add_alert {
    position: fixed;
    top: 50px;
    z-index: 1000;
    left: 40%;
}
.description {
    max-height: 100px;
    overflow: scroll;
}
.sub_rev {
    font-weight: 700;
    font-size: larger;
    cursor: pointer;
}



/* ////////////////////////////////////////////////////////////////////////// */

@media screen and (max-width: 500px){
    .productDetail {
        flex-direction: column;
        padding: 0;
    }
    .left {
        width: 80%;
        height: fit-content;
        display: flex;
        flex-direction: column;
        justify-content: center;
        /* display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center; */
        border: 1px solid rgb(218, 215, 215);
        margin-top: 110px;
        border-radius: 0;
    }
    .left img {
        max-width: 18rem;
        margin: 0 auto;
    }
    .right {
        display: flex;
        flex-direction: column;
        gap: 10px;
        width: 80%;
        border: 1px solid rgb(198, 193, 193);
        display: flex;
        flex-direction: column;
        color: aliceblue;
        word-wrap: break-word;
        max-height: 500px;
        text-overflow: ellipsis;
        margin-bottom: 100px;
        padding: 20px 0;
        padding-left: 20px;
        margin-top: 0;
    }
    .right > h1 {
        font-size: 20px;
    }
    .right > .p_id {
        display: none;
    }
    price {
        font-size: 30px;
    }
    .reviews {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        top: 0;
        width: 100vw;
        min-height: 100vh;
        background-color: #000000;
        clip-path: polygon(25% 0%, 75% 0%, 100% 5%, 100% 100%, 0 100%, 0 5%);
        margin-top: 100px;
    
    }
    .right  {
        margin-top: 30px;
        padding-left: 0;
        border: none;
    }
    .reviews {
        clip-path: polygon(0% 0%, 75% 0%, 100% 0%, 100% 100%, 0 100%, 0 5%);
        top: -30vw;
    }
    .head h1 {
        font-size: 40px;
    }
    .card {
        max-width: 90vw;
    }
}