.input {
    text-decoration: none;
}
.container {
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    background-color: rgba(0, 0, 0, 0.722);
    width: 100vw;
    height: 50px;
    z-index: 990;
}
.searchBox > input {
    height: 35px;
    width: 20vw;
    margin: 0 5px;
    border-radius: 20px;
    padding: 0 10px;
    background-color: #000000;
    color: aliceblue;
    border: 1px solid wheat;
}
.searchBox .btn {
    width: 100px;
    color: #000000;
    font-size: large;
    background-color: #04eeea;
    cursor: pointer;
}
.searchBox .btn:hover {
    background-color: #04eeea5b;

}
.login,.register{
    height: 35px;
    padding: 0 20px;
    margin: 0 10px;
    background-color: #15d170;
    border-radius: 20px;
    font-weight: 700;
    font-size: large;
    cursor: pointer;
}
.login {
    background-color: transparent;
    border: 2px solid #15d170;
    color: #15d170;
}
.login:hover {
    background-color: #15d170;
    color: black;
}
.register:hover {
    background-color: transparent;
    border: 2px solid #15d170;
    color: #15d170;
}
.nav_left {
    display: flex;
    align-items: center;
    gap: 20px;
}
.nav_left  h4 {
    color: aliceblue;
    border-bottom: 1px solid white;
}

.auth .speedDial {
    position: absolute;
    right: 40px;
    top: 0;
    z-index: 100;
    width: 0px;
}

.MuiButtonBase-root {
    width: 40px;
    height: 40px;
}
.nav_left ul {
    display: flex;
    gap: 20px;
}
.speedDial {
    width: 40px;
}
.css-7dv1rb-MuiButtonBase-root-MuiFab-root-MuiSpeedDial-fab {
    width: 40px;
    height: 40px;
}
.prog {
    position: absolute;
    left: 0;
    width: 1%;
    height: 100%;
    background-color: #f8054695;
    z-index: -1;
}
.nav_left > img {
    width: 70px;
}
.auth {
    display: flex;
    align-items: center;
    justify-content: center;
}
.search_main {
    width: 100vw;
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.search_icon {
    display: none;
}
.speedDialIcon {
    width: 50px;
    height: 50px;
    border-radius: 100%;
  }
/* ////////////////////////////////////////////////////////////////// */

@media screen and (max-width:600px) {

    .searchBox {
        display: none;
    }
    .nav_left h4 {
        font-size: 10px;
    }
    .nav_left {
        gap: 0;
    }
    .container {
        padding: 0;
    }
    .searchBox > input {
        height: 35px;
        width: 50vw;
        margin: 0;
        border-radius: 20px;
        padding: 0 10px;
        background-color: #000000;
        color: aliceblue;
        border: 1px solid wheat;
    }
    .searchBox .btn {
        width: 100px;
        color: #000000;
        font-size: large;
        background-color: #04eeea;
        cursor: pointer;
    }
    .search_icon {
        display: block;
        position: absolute;
        right: 120px;
    }


}